.btb-vue-list-layer {
  margin: 0;
  padding: 0;
}
.btb-vue-list-layer .layer_container {
  list-style: none;
}
.btb-vue-list-layer .container_entry {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
}
.btb-vue-list-layer .container_entry.entry-collapsable {
  cursor: pointer;
}
.btb-vue-list-layer .container_entry .entry_title {
  flex-grow: 1;
}
.btb-vue-list-layer .container_entry .entry_collapseBtn {
  flex-shrink: 0;
}
.btb-vue-list-layer .container_entry .entry_collapseBtn.collapseBtn-default {
  height: 1rem;
  width: 1rem;
  transition: transform 0.5s ease 0s;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btb-vue-list-layer .container_entry .entry_collapseBtn.collapseBtn-default .collapseBtn_arrow {
  border-color: currentColor transparent transparent;
  border-style: solid;
  border-width: 0.5rem 0.5rem 0;
  height: 0;
  width: 0;
}
.btb-vue-list-layer .container_entry .entry_collapseBtn.collapseBtn-default.collapseBtn-on {
  transform: rotate(90deg);
}
.btb-vue-list-layer .container_sublayer {
  overflow: hidden;
  transition: max-height 0.5s ease 0s;
}

